<template>
  <div id="app">
    <v-ons-navigator
      id="navigator"
      swipeable
      swipe-target-width="200px"
      :page-stack="pageStack"
      @push-page="pageStack.push($event)"
      @pop-page="popStatck"
    ></v-ons-navigator>
  </div>
</template>

<script>
import gamePage from "@/views/Game";
import { initAd, removeBanner } from "@/api/admob.js";
export default {
  name: "app",
  data() {
    return {
      pageStack: [gamePage]
    };
  },
  created() {
    this.$ons.disableAutoStyling();
    window.addEventListener("orientationchange", function() {
      if (window.innerHeight > window.innerWidth) {
        document.getElementsByTagName("body").style.transform = "rotate(90deg)";
      }
    });
  },
  mounted() {
    document.addEventListener("deviceready", function() {
      initAd();
    });
    setTimeout(() => {
      this.$ons.GestureDetector(document.getElementById("navigator")).dispose();
    }, 10);
    this.$store.dispatch("levels/setGameMode");
  },
  methods: {
    popStatck(){
      removeBanner();
      this.pageStack = [gamePage];
    },
  }
};
</script>


